import { getcartlist, updataCart, apidelcart } from "@/api/cart.js"


export default {
  // 开启命
  namespaced: true,
  state() {
    return {
      cartList: []
    }
  },
  // 修改数据的方法
  mutations: {
    seyCartList(state, value) {
      [
        state.cartList = value
      ]
    },
    changegoodschecked(state, id) {
      state.cartList.forEach(item => {
        if (item.goods_id === id) {
          item.checked = !item.checked
        }
      })
    },
    changeallchecked(state, value) {
      state.cartList.forEach(item => {
        item.checked = value
      })
    },

  },
  // 处理异步
  actions: {
    async getCartList(context) {
      let res = await getcartlist()
      res.list.forEach(item => {
        item.checked = true
      });
      context.commit('seyCartList', res.list)
    },
    async updataCart(context, obj) {
      await updataCart(obj.goodsid, obj.value, obj.goodsskuid)
      context.dispatch('getCartList')
    },
    async storedelcart(context) {
      // 这里是被选中的商品id
      const selectgoods = context.getters.selectGoods
      const selectgoodsid = selectgoods.map(item => item.id)
      await apidelcart(selectgoodsid)
      context.dispatch('getCartList')
    }
  },
  // 获取数据的方法
  getters: {
    goodsTotol(state) {
      let total = 0;
      state.cartList.forEach(item => {
        total += item.goods_num
      })
      return total
    },
    goodsCount(state) {
      let count = 0;
      state.cartList.forEach(item => {
        if (item.checked) {
          count += item.goods_num
        }
      })
      return count
    },
    goodsprice(state) {
      let price = 0;
      state.cartList.forEach(item => {
        if (item.checked) {
          price += item.goods.goods_price_min * item.goods_num
        }
      })
      return price.toFixed(2);
    },
    // 全选按钮的状态
    allchecked(state) {
      let checked = true;
      state.cartList.forEach(item => {
        if (!item.checked) {
          checked = false;
        }
      })
      return checked
    },
    selectGoods (state){
      return state.cartList.filter(item => item.checked)
    }
  }
}