<template>
  <div class="goods-btn">
    <button class="btn" @click="subgooods()">-</button>
    <button type="Number" class="content">{{ GoodsNum }}</button>
    <button class="btn" @click="addgoods()">+</button>
  </div>
</template>

<script>
export default {
  props: {
    GoodsNum: {
      type: Number,
      default: 1,
    },
  },
  methods: {
    subgooods() {
      if (this.GoodsNum > 1) {
        this.$emit("subgoods", this.GoodsNum - 1);
      }
    },
    addgoods() {
      this.$emit("addgoods", this.GoodsNum + 1);
    },
  },
};
</script>

<style scoped>
.goods-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 30px;
  border-radius: 5px;
}

.btn {
  width: 30px;
  height: 30px;
  background-color: #efefef;
  border: none;
  margin: 0 5px;
}

.content {
  width: 40px;
  height: 30px;
  border: none;
  text-align: center;
  color: #000000;
}
</style>